.header-border{
    position: relative;
    color: #fff!important;
    cursor:pointer;
}
.active{
    position: relative;
    
}
.header-border:after{
    height: 3px;
    width: 0%;
    background-color: #ff5f13;
    position: absolute;
    bottom: 5px;
    right: 0px;;
    content: "";
    transition: width 0.5s;
}
.active:after{
    height: 3px;
    width: 90%;
    background-color:#ff5f13;
    position: absolute;
    bottom: 5px;
    right: 3px;
    content: "";
    transition: width 0.5s; 
}
@media(max-width:768px){
   .navbar .header-border:hover:after{
        width: 15%;
    }
    .header-border:hover:after{
        width: 90%;
    }
   .navbar .active:after{
        width: 15%;
    }

}
@media(min-width:768px){
    .header-border:hover:after{
        width: 90%;
    } 
}

